  padding: 0;
  margin: 0;
  box-sizing: border-box;

  font-family: "Kanit", sans-serif;
  font-weight: 400;
  font-style: normal;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

a,
input,
button,
textarea {
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  background-color: #fff;
  color: #313131;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

.nav {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #313131;
}

.nav li {
  display: inline-block;
  padding: 10px;
}

.nav li a {
  text-decoration: none;
  color: #fff;
}

.brand {
  margin-right: auto;
  color: #fff;
}

.brand h2 {
  font-weight: 600;
}

.toggle_nav {
  color: #fff;
  margin-top: 10px;
  background-color: transparent;
  border: none;
}

.hero {
  min-height: 400px;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero-inner {
  margin: 0 auto;
}

.hero-text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

.hero-text h1 {
  margin-top: 5px;
  font-size: 50px;
  font-weight: 500;
}

.hero-text p {
  margin-top: 10px;
}

.hero-text button {
  margin-top: 15px;
  padding: 10px 10px 0px 10px;
  border-radius: 25px;
  border: 0;
  cursor: pointer;
}

.hero-text button a {
  font-size: 15px;
  text-decoration: none;
  color: #313131;
}

.content {
  padding: 20px;
}

/*
 * headline
 */

.headline__figure {
  width: 100%;
}

.headline__figure img {
  width: 100%;
}

.headline__figure figcaption {
  text-align: center;
  color: #666666;
  font-size: 13px;
  font-weight: 400;
  margin-top: 8px;
}

.headline__content {
  width: 100%;
  padding: 16px 0;
}

.headline__title {
  font-size: 24px;
  font-weight: 500;
}

.headline__description {
  font-size: 12px;
  margin-top: 12px;
}

.headline__button {
  text-transform: uppercase;
  margin-top: 24px;
  cursor: pointer;
  padding: 8px 16px;
  color: white;
  border: none;
  border-radius: 5px;
  background-color: #313131;
  transition: opacity 0.3s;
}

.headline__button:hover {
  opacity: 0.8;
}

.latest h1 {
  text-align: center;
  padding: 10px;
}

.product-item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 25px;
  overflow: hidden;
}

.product-item-content {
  padding: 20px;
}

.product-rating {
  font-weight: 600;
  font-size: 20px;
}

.product-section {
  position: relative;
}

.product-section h3.product-city {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: #313131;
  color: #fff;
  padding: 10px 20px 10px 10px;
  border-radius: 0px 0px 25px 0px;
}

.product-section img.product-image {
  width: 100%;
  display: block;
}

footer {
  padding: 10px;
  background-color: #313131;
}

footer p {
  text-align: center;
  color: #fff;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAEA;EACE,UAAU;EACV,SAAS;EACT,sBAAsB;;EAEtB,gCAAgC;EAChC,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;;EAEE,SAAS;EACT,UAAU;EACV,WAAW;EACX,YAAY;AACd;;AAEA;;;;EAIE,qBAAqB;EACrB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,OAAO;EACP,YAAY;EACZ,sBAAsB;EACtB,cAAc;EACd,YAAY;AACd;;AAEA;EACE,MAAM;AACR;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,aAAa;EACb,yBAAyB;AAC3B;;AAEA;EACE,qBAAqB;EACrB,aAAa;AACf;;AAEA;EACE,qBAAqB;EACrB,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,WAAW;AACb;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,gBAAgB;EAChB,6BAA6B;EAC7B,YAAY;AACd;;AAEA;EACE,iBAAiB;EACjB,WAAW;EACX,kHAAyH;EACzH,2BAA2B;EAC3B,4BAA4B;EAC5B,sBAAsB;EACtB,kBAAkB;AACpB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,kBAAkB;EAClB,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,YAAY;AACd;;AAEA;EACE,eAAe;EACf,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,2BAA2B;EAC3B,mBAAmB;EACnB,SAAS;EACT,eAAe;AACjB;;AAEA;EACE,eAAe;EACf,qBAAqB;EACrB,cAAc;AAChB;;AAEA;EACE,aAAa;AACf;;AAEA;;EAEE;;AAEF;EACE,WAAW;AACb;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,cAAc;EACd,eAAe;EACf,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,eAAe;AACjB;;AAEA;EACE,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,yBAAyB;EACzB,gBAAgB;EAChB,eAAe;EACf,iBAAiB;EACjB,YAAY;EACZ,YAAY;EACZ,kBAAkB;EAClB,yBAAyB;EACzB,wBAAwB;AAC1B;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,kBAAkB;EAClB,aAAa;AACf;;AAEA;EACE,0CAA0C;EAC1C,WAAW;EACX,mBAAmB;EACnB,gBAAgB;AAClB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,UAAU;EACV,yBAAyB;EACzB,WAAW;EACX,4BAA4B;EAC5B,+BAA+B;AACjC;;AAEA;EACE,WAAW;EACX,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,yBAAyB;AAC3B;;AAEA;EACE,kBAAkB;EAClB,WAAW;AACb","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@400;500;600;700&display=swap');\n\n* {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n\n  font-family: \"Kanit\", sans-serif;\n  font-weig